import { useParams } from "next/navigation";

const CategoryPage = () => {
    const { slug, subslug } = useParams() as { slug: string; subslug: string };
  return (
    <div>
      <h1>CategoryPage</h1> 
      <h3>{slug}</h3>
      <h3>{subslug}</h3>
    </div>
  )
}

export default CategoryPage